export const UploadButton = ({value, onChange}) => {
  const [uploading, setUploading] = useState(false);
  const [imgRef, setImgRef] = useState(null);

  const upload = async (ev) => {
    const file = ev.target.files[0];
    const type = file.type;
    let ext = 'jpg';
    if (type === 'image/jpeg') ext = 'jpg';
    if (type === 'image/gif') ext = 'gif';
    if (type === 'image/png') ext = 'png';
    if (file.size > 5 * 1024 * 1024) {
      alert('Lỗi: file quá lớn. Dung lượng tối đa cho phép là 5MB.');
      return;
    }
    setUploading(true);
    const imgRef = ref(storage, `tonghopevent/images/${Date.now()}.${ext}`);
    try {
      const snap = await uploadBytes(imgRef, file);
      const downloadURL = await getDownloadURL(snap.ref);
      // eslint-disable-next-line
      const url = downloadURL.replace(/&token=[A-Za-z0-9\-]+/, '');
      console.log(url);
      onChange(url);
    } catch (e) {
      alert(e);
    }
    setImgRef(imgRef);
    setUploading(false);
  };

  const deleteImage = () => {
    if (imgRef) {
      deleteObject(imgRef);
    }
    onChange(null);
  };

  if (uploading) return (
    <CircularProgress />
  );

  if (value) return (
    <div>
      <img src={value} alt="user uploaded" className="preview"></img>
      <Button variant="outlined" color="primary" onClick={deleteImage}>
        Xóa ảnh
      </Button>
    </div>
  );

  return (
    <Button component="label" variant="outlined" color="primary">
      <Backup style={{marginRight: '5px'}} /> Tải ảnh lên
      <input
        type="file"
        accept="image/png, image/gif, image/jpeg"
        onChange={upload}
        hidden
      />
    </Button>
  );
}