import React, { useState, useEffect } from 'react';
import Theme from '../Theme';
import Config from '../Config';
import axios from 'axios';
import '../App.css';
import '../bootstrap.css';
import './styles.css';
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import { FormQuestions } from './FormQuestions';

const classes = {
  input: {
    marginBottom: '20px',
    width: '500px',
    maxWidth: '100%'
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  cardStyle: {
    maxWidth: '700px',
    marginTop: '20px',
    marginRight: 'auto',
    marginLeft: 'auto',
  }
}

const getDefaultValues = () => {
  const values = {};
  for (const q of FormQuestions) {
    values[q.name] = q.defaultValue();
  }
  return values;
};

const getUserContext = () => {
  try {
    const data = decodeURIComponent(window.location.href.match(/data=([^&]+)/)[1]);
    const [hashPsid, org] = data.split(';');
    return {
      psid: hashPsid.substr(0, hashPsid.length - 5),
      hash: hashPsid.substr(-5),
      org: org.toLowerCase(),
    };
  } catch (e) {
    alert('ERROR: NO CONTEXT');
    return null;
  }
};
const CONTEXT = getUserContext();
console.log(CONTEXT);

const submitForm = async (doc) => {
  try {
    const data = {...doc, ...CONTEXT};
    // on backend: validate & remove "hash", add "created": Date.now()
    await axios.post('https://webhook.site/49f8889c-0e75-45dc-957f-4a349262219f', data);
    return true;
  } catch (e) {
    console.error(e);
    return false;
  }
};

const Form = () => {
  const [values, setValues] = useState(getDefaultValues());
  const [errors, setErrors] = useState({});
  const [hasError, setHasErrors] = useState(false);
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(true);
  const questionElements = [];

  for (const q of FormQuestions) {
    if (success) break;
    // q does not depends on other question
    // OR q does depends on other question and that question is true
    if (!q.dependsOn || (q.dependsOn && values[q.dependsOn])) {
      const name = q.name;
      const e = q.element({
        value: values[name],
        key: name,
        onChange: (val) => {
          setValues({ ...values, [name]: val });
          setErrors({ ...errors, [name]: null });
        },
        error: errors[name],
      });
      questionElements.push(e);
    }
  }

  const checkIfHasError = (errors) => {
    return Object.values(errors).some(t => !!t);
  };

  const onSubmit = async () => {
    const doc = {};
    const errors = {};
    for (const q of FormQuestions) {
      const name = q.name;
      const v = values[name];
      errors[name] = q.checkError(v);
      q.populateToDoc(v, doc);
    }
    setErrors(errors);
    // make sure there is no error
    if (!checkIfHasError(errors)) {
      setLoading(true);
      console.log(doc);
      const success = await submitForm(doc);
      setSuccess(success);
      setLoading(false);
      if (!success) {
        alert('Có lỗi xảy ra, xin vui lòng thử lại!');
      }
    }
  };

  useEffect(() => {
    setHasErrors(checkIfHasError(errors));
  }, [errors]);

  return (
    <MuiThemeProvider theme={Theme}>
      <CssBaseline />
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" style={{flexGrow: 1}}>
            Form nhập dữ liệu
          </Typography>
        </Toolbar>
      </AppBar>
      <div className="mainView">
        <Card style={classes.cardStyle}>
          <CardContent>
            <Typography variant="h5" component="h2" gutterBottom={true}>
              Form nhập dữ liệu hỗ trợ truyền thông
            </Typography>
            {!success && <Typography variant="subtitle1" color="textSecondary" gutterBottom={true}>
              Hãy nhập thông tin về dự án, cuộc thi hay sự kiện của bạn vào form dưới đây. Sau đó, chúng mình sẽ đăng bài giới thiệu trên page fanpage.
            </Typography>}
            {!success && <Typography variant="body1" component="small" color="textSecondary">
              (Các ô có dâu * là bắt buộc)
            </Typography>}
            {success && <Typography variant="subtitle1" color="textSecondary" gutterBottom={true}>
              Dữ liệu bạn nhập đã được lưu lại.<br/>Chúng mình sẽ thông báo với bạn ngay khi bài đăng được duyệt!
            </Typography>}
          </CardContent>        
        </Card>

        {questionElements}

        {!success && <Card style={classes.cardStyle}>
          <CardContent>
            <div>
              <small>Đừng quên bấm nút "Hoàn thành" khi bạn đã điền xong nhé:<br/><br/></small>
              <Button 
                color="primary"
                variant="contained"
                onClick={onSubmit}
                disabled={hasError}
              >Hoàn thành</Button>
              {hasError && <p className="error">
                Vẫn còn một vài ô điền có vấn đề. Bạn hãy kiểm tra lại nhé.
              </p>}
              <br/>
              <br/>
              <small>
                ------<br/>
                😸 Jiji: Hệ thống form điền này đang trong giai đoạn thử nghiệm.<br/>Nếu bạn gặp lỗi gì, hãy inbox cho Jiji nhé!
              </small>
            </div>
          </CardContent>        
        </Card>}

        {/* Copyright */}
        <center>
          <br/><br/>
          Developed by&nbsp;
          <b><a href="https://ngxson.com" target="_blank" rel="noopener noreferrer">Nui</a></b><br/>
          Version {Config.VERSION}
          <br/><br/>
        </center>
      </div>

      {loading && <div className="loading-overlay">
        <div>
          <CircularProgress /><br/>
          Đang gửi dữ liệu...
        </div>
      </div>}
    </MuiThemeProvider>
  );
};

export default Form;